<template>
  <div class="container" style="height:100%">
    <el-container style="height: 100%;">
      <el-header style="font-size: 14px; background-color:#25374F; height: 45px;" id="header">
        <div style="text-align: center; margin-left:-10px">
          <div style="width:180px">
            <div style="height: 45px; float:left;">
              <icon name="dashboard" scale="2.5"  height="45px"></icon>
            </div>
            <div style="height: 45px; float:left;">
              <span style="font-size: 15px; font-weight:bold; margin-left:0px; color:#FFF">&nbsp;可视化数据分析系统</span>
            </div>
          </div>
          <div style="height: 45px; float:left; margin-left:20px">
            <icon name="open" id="right" scale="1.5" style="height: 45px; cursor: pointer;" @click.native="collapse"></icon>
          </div>
        </div>
      </el-header>

      <el-container>
        <div style="background-color: #25374F; position:relative; height:calc(100vh - 45px); overflow:auto">
          <el-menu
            id = "menu"
            class="el-menu-vertical-demo"
            :router="true"
            :unique-opened="true"
            :default-active=default_active
            style="background-color: #25374F; border:0px; "
            :collapse="isCollapse"
            active-text-color="#FFF"
            background-color="#25374F"
            text-color="#FFF">
            <el-menu-item index="/season" v-if="dashboardShow">
              <icon name="data" scale="1.8" style="padding-right: 5px"></icon>
              <span slot="title">故障工单区域分析</span>
            </el-menu-item>
            <el-submenu index="1" >
              <template slot="title">
                <icon name="search" scale="1.8" style="padding-right: 5px;"></icon>
                <span slot="title">专题分析</span>
              </template>
              <el-menu-item index="/weekly" v-if="weekly">周报分析</el-menu-item>
              <el-menu-item index="/monthly" v-if="monthly">月报分析</el-menu-item>
              <el-menu-item index="/quarterly" v-if="quarterly">季报分析</el-menu-item>
              <el-menu-item index="/yearly" v-if="yearly">年报分析</el-menu-item>
            </el-submenu>
            <el-menu-item index="" v-if="professionalAnalysisShow">
              <icon name="draw" scale="1.8" style="padding-right: 5px"></icon>
              <span slot="title">专业分析</span>
            </el-menu-item>
            <el-menu-item index="" v-if="historicalDataShow">
              <icon name="database" scale="1.8" style="padding-right: 5px"></icon>
              <span slot="title">历史数据</span>
            </el-menu-item>
            <el-menu-item index="" v-if="trendAnalysisShow">
              <icon name="trend" scale="1.8" style="padding-right: 5px"></icon>
              <span slot="title">趋势分析</span>
            </el-menu-item>
          </el-menu>
        </div>
        <el-container>
          <el-main style="overflow:auto; height:calc(100vh - 45px); width:180px;">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
            </transition>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: '',
      username: '',
      openeds: ['2'],
      isCollapse: false,
      default_active: '/season',
      deg: 0,
      dashboardShow: true,
      specializedAnalysisShow: true,
      weekly: true,
      monthly: true,
      quarterly: true,
      yearly: true,
      professionalAnalysisShow: true,
      historicalDataShow: true,
      trendAnalysisShow: true
    }
  },
  // props: ['isCollapse'],
  methods: {
    showMenu () {
      let menuArr = sessionStorage.getItem('privilege')
      if (menuArr.indexOf('/season') !== -1) { this.dashboardShow = true }
      if (menuArr.indexOf('/season') !== -1) { this.specializedAnalysisShow = true }
      if (menuArr.indexOf('/season') !== -1) { this.professionalAnalysisShow = true }
      if (menuArr.indexOf('/season') !== -1) { this.historicalDataShow = true }
      if (menuArr.indexOf('/season') !== -1) { this.trendAnalysisShow = true }
    },

    selectItem () {
      this.default_active = this.$route.path
    },

    collapse () {
      this.isCollapse = !this.isCollapse
      this.$store.dispatch('change')
      this.deg += 90
      document.getElementById('right').style.transform = 'rotate(' + this.deg + 'deg)'
    }
  },
  created () {
    // this.showMenu()
    this.selectItem()
  },
  mounted () {
    // this.showMenu()
    this.selectItem()
  }
}
</script>

<style lang="scss" scoped>
  ::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 5px;
    //-webkit-box-shadow: inset 0 0 3px rgba(0,0,0,0.3);
    //background: rgba(255,255,255,0.3);
    background: #D6D8DD
  }
  /*   ::-webkit-scrollbar
    {
      width: 10px;
    } */

  #app {
    height: 100%;
  }

  html, body {
    margin: 0;
    height: 100%;
  }

  .el-header {
    line-height: 45px;
  }
  .el-menu-item {
    height:45px;
    line-height:43px;
  }

  .el-menu-item.is-active {
    //background-color: #E43C59!important;
    background-color:#C12E34!important;
  }

  .el-submenu {
    .el-menu-item {
      min-width: 100px;
    }
  }

  ul > li.el-submenu > ul > li.el-submenu > ul > li {
    margin-left:-20px;
  }

  .el-menu--horizontal .el-submenu>.el-menu {
    height:45px;
  }
  .el-dropdown-link {
    cursor: pointer;
    color:#FFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 180px;
  }
  .el-menu--collapse {
    width: 52px;
    margin-left:-5px;
  }

</style>

<style lang="scss">
  .el-submenu__title {
    height: 45px;
    line-height:43px;
  }
  .el-menu--horizontal .el-submenu .el-submenu__title {
    height:45px;
    line-height:43px;
  }
  .el-badge__content.is-fixed.is-dot {
    top:5px;
  }
  .el-dialog__title {
    font-size: 16px;
  }
  .el-form-item__label {
    font-size: 13px;
  }
  /*   .el-table {
      th {
        background-color: #FFA940;
        color: #FFF;
      }
      td {
        background-color: #FCFCFC;
      }
    } */
</style>



